import React, {useEffect} from 'react';
import CustomTextField from '../../Layout/CustomTextField';
import { replaceAllWhiteSpaces } from '../../../../utils/regexFuncs';

const TaxFileNumber = ({tfn, tfn_exemption, onChangeIn, errMsgs}) => {
  
  useEffect(()=>{
    return ()=>{
      onChangeIn({name:'tfn', value:''})
      onChangeIn({name:'tfn_exemption', value:''})
    }
  },[])

  const onChangeTFN = ({name, value}) => {
    const trimmedVal = replaceAllWhiteSpaces(value)
    onChangeIn({name, value:trimmedVal})
  }
  
  return (
    <div className="sub__form u-padding-10y"> 
        <CustomTextField 
            id="tax-file-number-1"
            label="Tax File Number (TFN)"
            placeholder="Tax File Number (TFN)"
            asterisk=""
            name='tfn'
            value={tfn}
            onChangeFn={onChangeTFN}
            formErrors={errMsgs}
        />
        <CustomTextField 
            id="tax-file-number-exemption-1"
            label="TFN Exemption (if applicable)"
            placeholder="TFN Exemption"
            asterisk=""
            name='tfn_exemption'
            value={tfn_exemption}
            onChangeFn={onChangeIn}
            formErrors={errMsgs}
          />
        <div className="text__box">
            <p className="text__box--p">It is not against the law if you choose not to provide your Tax File Number (TFN) or exemption reason. However, please note that should you decide not to, tax may be deducted from your distribution at the highest marginal tax rate (plus Medicare levy).</p>
        </div>
    </div>
  )
}

export default TaxFileNumber