import React, { useEffect } from 'react';
import { renderErrorTxt } from '../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const NoTINReasonExplanation = ({ errMsgs, investorId, investorDetails, noTinReasonInp }) => {

    useEffect(() => {
        return () => {
            noTinReasonInp({
                target:{
                    name:'explanation_for_tin_not_issued',
                    value:''
                }});
        }
    }, []);

  return (
    <div className="form-input form-input-full-width u-padding-y u-margin-bottom-0">
        <label htmlFor={`sec-g-con-1-exp${investorId}`}>Please provide explanation</label>
        <input className="form-input-common" type="text" placeholder="Please provide explanation" id={`sec-g-con-1-exp${investorId}`} required name='explanation_for_tin_not_issued' value={investorDetails?.tax_info?.explanation_for_tin_not_issued} onChange={noTinReasonInp}/>
    
        {errMsgs?.explanation_for_tin_not_issued?.length? <div className="input__desc">
        {renderErrorTxt(errMsgs?.explanation_for_tin_not_issued)} </div>: null}
    </div>
  )
}

export default NoTINReasonExplanation;