import React from 'react';
import { useSelector } from 'react-redux';
import NumberOfInvestors from './SubSections/NumberOfInvestors';
import TaxInformation from './SubSections/TaxInformation';
import IdentificationDocs from './SubSections/IdentificationDocs';
import {formatAndPrintDocs} from '../../../../utils/validations/helperFunc';

const SectionCContent = ({onFormChange, investorFormId, removeDocFromArr}) => {

  const secC = useSelector((store) => store.secC);
  
  return (
    <div className="section">
        <h2 className="section__heading">Section C: Individual(s) / Joint Investors / Individual Trustee(s)</h2>
        <ul>
            <li>Do not complete Section C if you have previously provided identification documents.</li>
            <li>Please complete Section C if trustee is an individual.</li>
        </ul>

        <NumberOfInvestors onFormChange={onFormChange} investorFormId={investorFormId}/>

        <TaxInformation onFormChange={onFormChange}/>

        <IdentificationDocs onFormChange={onFormChange} investorFormId={investorFormId}/>

        {secC?.documents?.length > 0? formatAndPrintDocs(secC?.documents, removeDocFromArr): null}

    </div>
  )
}

export default SectionCContent;