import React, {useEffect} from 'react';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import { YES_C, NO_C } from '../../../../../constants';

function TaxInfoOtherJurisRadioBtns({investorId, investorDetails, cnUnderSchemeRadioIn, othrJurRadioIn, othrJurDaysRadioIn, incmTaxOthrJur, errMsgs}) {
  
  useEffect(()=>{
    return ()=>{
        cnUnderSchemeRadioIn({value:''})
        othrJurRadioIn({value:''})
        othrJurDaysRadioIn({value:''})
        incmTaxOthrJur({value:''})
    }
  }, [])
  
    return (
    <div className="u-padding-10y">
        <div className="u-padding-10y">
            <h6>1. Did you obtain residence rights under a Citizenship by Investment (CBI) or Residence by Investment (RBI) scheme?<span>*</span></h6>
            <CustomRadioInput 
                name={`rights-cbi-rbi${investorId}`}
                id={`rights-cbi-rbi-yes${investorId}`}
                content="Yes"
                val={YES_C}
                checked={investorDetails?.tax_info?.has_obtained_residence_rights_under_CBI_or_RBI === YES_C}
                onChangeFn={cnUnderSchemeRadioIn}
            />
            <CustomRadioInput
                name={`rights-cbi-rbi${investorId}`}
                id={`rights-cbi-rbi-no${investorId}`}
                content="No"
                val={NO_C}
                checked={investorDetails?.tax_info?.has_obtained_residence_rights_under_CBI_or_RBI === NO_C}
                onChangeFn={cnUnderSchemeRadioIn}
            />
            {errMsgs?.has_obtained_residence_rights_under_CBI_or_RBI?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs?.has_obtained_residence_rights_under_CBI_or_RBI)}
            </div>: null}
        </div>

        <div className="u-padding-10y">
            <h6>2. Do you hold residence rights in any other jurisdiction(s)?<span>*</span></h6>
            <CustomRadioInput 
                name={`other-jurisdictions${investorId}`}
                id={`other-jurisdictions-yes${investorId}`}
                content="Yes"
                val={YES_C}
                checked={investorDetails?.tax_info?.has_hold_residence_rights_in_other_jurisdictions === YES_C}
                onChangeFn={othrJurRadioIn}
            />
            <CustomRadioInput
                name={`other-jurisdictions${investorId}`}
                id={`other-jurisdictions-no${investorId}`}
                content="No"
                val={NO_C}
                checked={investorDetails?.tax_info?.has_hold_residence_rights_in_other_jurisdictions === NO_C}
                onChangeFn={othrJurRadioIn}
            />
            {errMsgs?.has_hold_residence_rights_in_other_jurisdictions?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs?.has_hold_residence_rights_in_other_jurisdictions)}
            </div>: null}
        </div>

        <div className="u-padding-10y">
            <h6>3. Have you spent more than 90 days in any other jurisdiction(s) during the previous year?<span>*</span></h6>
            <CustomRadioInput 
                name={`90-days-other-jurisdictions${investorId}`}
                id={`90-days-other-jurisdictions-yes${investorId}`}
                content="Yes"
                val={YES_C}
                checked={investorDetails?.tax_info?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year === YES_C}
                onChangeFn={othrJurDaysRadioIn}
            />
            <CustomRadioInput
                name={`90-days-other-jurisdictions${investorId}`}
                id={`90-days-other-jurisdictions-no${investorId}`}
                content="No"
                val={NO_C}
                checked={investorDetails?.tax_info?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year === NO_C}
                onChangeFn={othrJurDaysRadioIn}
            />
            {errMsgs?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year)}
            </div>: null}
        </div>

        <div className="u-padding-10y">
            <h6>4. Have you filed income tax returns in any other jurisdiction(s) during the previous year?<span>*</span></h6>
            <CustomRadioInput 
                name={`tax-returns-other-jurisdictions${investorId}`}
                id={`tax-returns-other-jurisdictions-yes${investorId}`}
                content="Yes"
                val={YES_C}
                checked={investorDetails?.tax_info?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year === YES_C}
                onChangeFn={incmTaxOthrJur}
            />
            <CustomRadioInput
                name={`tax-returns-other-jurisdictions${investorId}`}
                id={`tax-returns-other-jurisdictions-no${investorId}`}
                content="No"
                val={NO_C}
                checked={investorDetails?.tax_info?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year === NO_C}
                onChangeFn={incmTaxOthrJur}
            />
            {errMsgs?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year)}
            </div>: null}
        </div>
    </div>
  )
}

export default TaxInfoOtherJurisRadioBtns