import React from 'react';
import { useSelector } from 'react-redux';
import Investors from '../Investors';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const NumberOfInvestors = ({ onFormChange, investorFormId}) => {
    const secC = useSelector((store) => store.secC);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const onChangeInvestor = ({ name, value, level, investorId }) => {
        onFormChange({ 
            name,
            value,
            level,
            investor_id: investorId
        });
    }

    const renderInvestors = () => { 
        return secC?.investors_details?.map((eachItem, index)=>{
            const tempIndex = index+1
            const uniqId = eachItem?.investor_id || tempIndex //eachItem?.id
            if(index < secC.no_of_investors ){
                return <Investors key={`${uniqId}`}
                        investorId={uniqId}
                        investorDetails={eachItem}
                        investorFormId={investorFormId} 
                        onChangeInvestor={onChangeInvestor} itemIndex={index}/> 
            }
            return null
        })
    }

    const onChangeFn = ({name, value}) =>{
        const convrtToNum = parseInt(value);
        onFormChange({name, value:convrtToNum, level:'no_of_investors'});
    }

  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">C.1 Personal Details</h4>

        <div className="section__container--box">
            <div>
                <h6>Number of Investors (or individual trustees, if applicable)?<span>*</span></h6>
                <div className="row-sm row-space-between">
                    <CustomRadioInput
                        name="no_of_investors" 
                        id="num-of-investors-1" 
                        content="1" 
                        val="1"
                        checked={secC.no_of_investors === 1}
                        onChangeFn={onChangeFn}
                    />
                    <CustomRadioInput
                        name="no_of_investors" 
                        id="num-of-investors-2" 
                        content="2" 
                        val="2" 
                        checked={secC.no_of_investors === 2}
                        onChangeFn={onChangeFn}            
                    />
                    <CustomRadioInput
                        name="no_of_investors" 
                        id="num-of-investors-3" 
                        content="3" 
                        val="3" 
                        checked={secC.no_of_investors === 3}
                        onChangeFn={onChangeFn}
                    />
                </div>
                {invFormStatus.errors?.no_of_investors? <div className="input__desc">
                    {renderErrorTxt(invFormStatus.errors?.no_of_investors)}
                </div>: null}
            </div>

            {renderInvestors()}
        </div>

        {/* {Array.from({ length: secC.no_of_investors }, (_, index) => <Investors key={index} investorId={index} />)} */}
    </div>
  )
}

export default NumberOfInvestors;