import React from 'react';
import { useSelector } from 'react-redux';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
// import CustomTextField from '../../../Layout/CustomTextField';
// import CustomCheckbox from '../../../Layout/CustomCheckbox';
import TaxFileNumber from '../TaxFileNumber';
import { YES_C, NO_C, TAX_RESIDENCE_CHKBX_OTHER_C } from '../../../../../constants';
import TaxResidentNonAus from './TaxResidentNonAus';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const TaxResidentInformation = ({ investorId, investorDetails, onChangeTaxInfo, itemIndex }) => {

    const invFormStatus = useSelector((store) => store.invFormStatus)

    const errMsgs = {
        is_tax_resident_of_aus_1: invFormStatus.errors?.is_tax_resident_of_aus_1 ? invFormStatus.errors?.is_tax_resident_of_aus_1[itemIndex] : [],
        tfn: invFormStatus.errors?.tfn ? invFormStatus.errors?.tfn[itemIndex] : [],
        tfn_exemption: invFormStatus.errors?.tfn_exemption ? invFormStatus.errors?.tfn_exemption[itemIndex] : [],
        is_tax_resident_of_us: invFormStatus.errors?.is_tax_resident_of_us ? invFormStatus.errors?.is_tax_resident_of_us[itemIndex] : [],
        is_tax_resident_of_non_aus_country: invFormStatus.errors?.is_tax_resident_of_non_aus_country ? invFormStatus.errors?.is_tax_resident_of_non_aus_country[itemIndex] : [],
        country_of_tax_resident: invFormStatus.errors?.country_of_tax_resident ? invFormStatus.errors?.country_of_tax_resident[itemIndex] : [],
        tax_residence_countries: invFormStatus.errors?.tax_residence_countries ? invFormStatus.errors?.tax_residence_countries[itemIndex] : [],
        tin: invFormStatus.errors?.tin ? invFormStatus.errors?.tin[itemIndex] : [],
        reason_for_no_tin: invFormStatus.errors?.reason_for_no_tin ? invFormStatus.errors?.reason_for_no_tin[itemIndex] : [],
        explanation_for_tin_not_issued: invFormStatus.errors?.explanation_for_tin_not_issued ? invFormStatus.errors?.explanation_for_tin_not_issued[itemIndex] : [],
        has_obtained_residence_rights_under_CBI_or_RBI: invFormStatus.errors?.has_obtained_residence_rights_under_CBI_or_RBI ? invFormStatus.errors?.has_obtained_residence_rights_under_CBI_or_RBI[itemIndex] : [],
        has_hold_residence_rights_in_other_jurisdictions: invFormStatus.errors?.has_hold_residence_rights_in_other_jurisdictions ? invFormStatus.errors?.has_hold_residence_rights_in_other_jurisdictions[itemIndex] : [],
        has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year: invFormStatus.errors?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year ? invFormStatus.errors?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year[itemIndex] : [],
        has_filed_tax_returns_in_other_jurisdiction_in_prev_year: invFormStatus.errors?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year ? invFormStatus.errors?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year[itemIndex] : [],
    }

    const onChangeIn = ({ name, value }) => {
        onChangeTaxInfo({ name, value, level: 'tax_info', investorId })
    }

    // const taxResCountriesVal = () => {
    //     return investorDetails?.tax_info?.country_of_tax_resident.toString();
    // }

    const onChangeTaxResidence = ({ name, value }) => {
        const convrtToNum = value && parseInt(value);
        const activeItems = investorDetails?.tax_info?.tax_residence_countries ? [...investorDetails?.tax_info?.tax_residence_countries] : [];
        const isNoneOfAbove = activeItems.indexOf(TAX_RESIDENCE_CHKBX_OTHER_C); //check if has None of the above option
        if (convrtToNum === TAX_RESIDENCE_CHKBX_OTHER_C) {
            activeItems.splice(0, activeItems.length); //remve everythin from the array
            isNoneOfAbove === -1 && activeItems.push(TAX_RESIDENCE_CHKBX_OTHER_C);//removed everything and add only none-of-above val

        } else {
            const indexOfItem = activeItems.indexOf(convrtToNum); //get index if item is there inside list

            isNoneOfAbove > -1 && activeItems.splice(isNoneOfAbove, 1); //remove the element from array if already present
            indexOfItem > -1 && activeItems.splice(indexOfItem, 1); //remove the element from array if already present
            indexOfItem === -1 && activeItems.push(convrtToNum); //add element to the array
        }
        onChangeIn({ name, value: [...activeItems] });
        return;
    }

    const noTinReasonDropDwn = (e) => {
        const { name, value } = e.target;
        const convrtToNum = value && parseInt(value);
        onChangeIn({ name, value: convrtToNum })
    }

    const noTinReasonInp = (e) => {
        const { name, value } = e.target;
        onChangeIn({ name, value })
    }

    //citizen other than aus
    const otherCnRadioIn = ({name, value}) => {
        if((investorDetails?.tax_info?.is_tax_resident_of_aus_1 === NO_C) || (investorDetails?.tax_info?.is_tax_resident_of_us === YES_C)){
            value = YES_C
        }
        const convrtToNum = parseInt(value);
        onChangeTaxInfo({ name: 'is_tax_resident_of_non_aus_country', value: convrtToNum, level: 'tax_info', investorId })
    }

    //are you a resident of aus
    const ausResRadioIn = ({name, value}) => {
        const convrtToNum = parseInt(value);
        convrtToNum === NO_C && otherCnRadioIn({ value: YES_C });
        onChangeTaxInfo({name:'is_tax_resident_of_aus_1', value:convrtToNum, level:'tax_info', investorId})
    }

    //us citizen
    const usCitzRadioIn = ({ name, value }) => {
        const convrtToNum = parseInt(value);
        convrtToNum === YES_C && otherCnRadioIn({ value: YES_C })
        onChangeTaxInfo({ name: 'is_tax_resident_of_us', value: convrtToNum, level: 'tax_info', investorId })
    }
    //citizen under CBI or RBI
    const cnUnderSchemeRadioIn = ({ name, value }) => {
        const convrtToNum = value && parseInt(value);
        onChangeTaxInfo({ name: 'has_obtained_residence_rights_under_CBI_or_RBI', value: convrtToNum, level: 'tax_info', investorId })
    }
    //residence in other jurisdictions
    const othrJurRadioIn = ({ name, value }) => {
        const convrtToNum = value && parseInt(value);
        onChangeTaxInfo({ name: 'has_hold_residence_rights_in_other_jurisdictions', value: convrtToNum, level: 'tax_info', investorId })
    }
    //+90 days in other jurisdictions
    const othrJurDaysRadioIn = ({ name, value }) => {
        const convrtToNum = value && parseInt(value);
        onChangeTaxInfo({ name: 'has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year', value: convrtToNum, level: 'tax_info', investorId })
    }
    //income tax returns in othr jurisdictions
    const incmTaxOthrJur = ({ name, value }) => {
        const convrtToNum = value && parseInt(value);
        onChangeTaxInfo({ name: 'has_filed_tax_returns_in_other_jurisdiction_in_prev_year', value: convrtToNum, level: 'tax_info', investorId })
    }

    return (
        <div className="section__container--box" key={investorId}>
            <h5>{`Investor ${itemIndex + 1}`}</h5>

            <div className="u-padding-10y">
                <h6>Are you a tax resident of Australia?<span>*</span></h6>
                <div >
                    <CustomRadioInput
                        name={`sec-c-tax-resident-${investorId}`}
                        id={`is-tax-resident${investorId}-yes`}
                        content="Yes"
                        val={YES_C}
                        checked={investorDetails?.tax_info?.is_tax_resident_of_aus_1 === YES_C}
                        onChangeFn={ausResRadioIn}
                    />
                    <CustomRadioInput
                        name={`sec-c-tax-resident-${investorId}`}
                        id={`is-tax-resident${investorId}-no`}
                        content="No"
                        val={NO_C}
                        checked={investorDetails?.tax_info?.is_tax_resident_of_aus_1 === NO_C}
                        onChangeFn={ausResRadioIn}
                    />
                    {errMsgs?.is_tax_resident_of_aus_1?.length ? <div className="input__desc">
                        {renderErrorTxt(errMsgs.is_tax_resident_of_aus_1)}
                    </div> : null}
                </div>
                {investorDetails?.tax_info?.is_tax_resident_of_aus_1 === YES_C ? <TaxFileNumber tfn={investorDetails?.tax_info?.tfn} tfn_exemption={investorDetails?.tax_info?.tfn_exemption} onChangeIn={onChangeIn} errMsgs={errMsgs} /> : null}
            </div>

            <div className="u-padding-10y">
                <h6>Are you a US citizen, US tax resident or currently residing in the US?<span>*</span></h6>

                <div >
                    <CustomRadioInput
                        name={`sec-c-is-citizen-${investorId}`}
                        id={`is-citizen${investorId}-yes`}
                        content="Yes"
                        val={YES_C}
                        checked={investorDetails?.tax_info?.is_tax_resident_of_us === YES_C}
                        onChangeFn={usCitzRadioIn}
                    />
                    <CustomRadioInput
                        name={`sec-c-is-citizen-${investorId}`}
                        id={`is-citizen${investorId}-no`}
                        content="No"
                        val={NO_C}
                        checked={investorDetails?.tax_info?.is_tax_resident_of_us === NO_C}
                        onChangeFn={usCitzRadioIn}
                    />
                    {errMsgs?.is_tax_resident_of_us?.length ? <div className="input__desc">
                        {renderErrorTxt(errMsgs.is_tax_resident_of_us)}
                    </div> : null}
                </div>
            </div>

            <div className="u-padding-10y">
                <h6>Are you a tax resident of a country other than Australia?<span>*</span></h6>

                <CustomRadioInput
                    name={`sec-c-not-aus-tax-resident-${investorId}`}
                    id={`is-ano-tax-resident${investorId}-yes`}
                    content="Yes"
                    val={YES_C}
                    checked={investorDetails?.tax_info?.is_tax_resident_of_non_aus_country === YES_C}
                    onChangeFn={otherCnRadioIn}
                />
                <CustomRadioInput
                    name={`sec-c-not-aus-tax-resident-${investorId}`}
                    id={`is-ano-tax-resident${investorId}-no`}
                    content="No"
                    val={NO_C}
                    checked={investorDetails?.tax_info?.is_tax_resident_of_non_aus_country === NO_C}
                    onChangeFn={otherCnRadioIn}
                />
                {errMsgs?.is_tax_resident_of_non_aus_country?.length ? <div className="input__desc">
                    {renderErrorTxt(errMsgs.is_tax_resident_of_non_aus_country)}
                </div>: null}
            </div>
        
        {investorDetails?.tax_info?.is_tax_resident_of_non_aus_country === YES_C || investorDetails?.tax_info?.is_tax_resident_of_us === YES_C? 
        <TaxResidentNonAus
        investorId={investorId}
        investorDetails={investorDetails}
        onChangeIn={onChangeIn}
        noTinReasonDropDwn={noTinReasonDropDwn}
        noTinReasonInp={noTinReasonInp}
        onChangeTaxResidence={onChangeTaxResidence}
        cnUnderSchemeRadioIn={cnUnderSchemeRadioIn}
        othrJurRadioIn={othrJurRadioIn}
        othrJurDaysRadioIn={othrJurDaysRadioIn}
        incmTaxOthrJur={incmTaxOthrJur}
        errMsgs={errMsgs}/>:null}

        {investorDetails?.tax_info?.is_tax_resident_of_us === YES_C?
            <div className="note__box u-padding-0">
                <p className="note__box--p u-margin-bottom-0">Please note that if you are a US citizen or resident, we may require further information or we may not be able to accept your application.</p>
            </div>:null}
    </div>
  )
}

export default TaxResidentInformation;