import React, {useEffect} from 'react';
import Form from 'react-bootstrap/Form';
import {SEC_C_NO_TIN_REASON_A, SEC_C_NO_TIN_REASON_B, SEC_C_NO_TIN_REASON_C} from '../../../../../constants'
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import NoTINReasonExplanation from '../NoTINReasonExplanation';

function NoTinReason({ investorId, noTinReasonDropDwn, investorDetails, errMsgs, noTinReasonInp }) {
    const A="Reason A:  Country of residence does not issue TINs to tax residents"
    const B="Reason B: Individual has not been issued with a TIN"
    const C="Reason C: Country of residence does not require the TIN to be disclosed"

    useEffect(()=>{
        return ()=>{
            noTinReasonDropDwn({target:{name:'reason_for_no_tin', value:''}})
            noTinReasonInp({target:{name:'explanation_for_tin_not_issued', value:''}})            
        }
    },[])

  return (
    <div className="u-margin-bottom dropdown-global">
                <label htmlFor={`reason${investorId}`}>If no TIN, list reason <span>*</span></label>
                <Form.Control
                className="u-margin-bottom"
                as="select"
                id={`reason${investorId}`}
                name="reason_for_no_tin"
                value={investorDetails?.tax_info?.reason_for_no_tin}
                onChange={noTinReasonDropDwn}>
                    <option></option>
                    <option value={SEC_C_NO_TIN_REASON_A}>{A}</option>
                    <option value={SEC_C_NO_TIN_REASON_B}>{B}</option>
                    <option value={SEC_C_NO_TIN_REASON_C}>{C}</option>
                </Form.Control>

                {errMsgs?.reason_for_no_tin?.length? <div className="input__desc">
                    {renderErrorTxt(errMsgs?.reason_for_no_tin)}
                </div>: null}

                {investorDetails?.tax_info?.reason_for_no_tin === SEC_C_NO_TIN_REASON_B? 
                <NoTINReasonExplanation 
                errMsgs={errMsgs}
                investorId={investorId}
                investorDetails={investorDetails}
                noTinReasonInp={noTinReasonInp} renderErrorTxt={renderErrorTxt}/>: null}
            </div>
  )
}

export default NoTinReason