import React, { useEffect } from 'react';
import CustomTextField from '../../Layout/CustomTextField';

const IdentifyPEP = ({ onChangeIn, investorId, investorDetails, errors }) => {


    useEffect(() => {
        return () => {
            onChangeIn({ name: 'pep_name', value: '' })
            onChangeIn({ name: 'pep_position', value: '' }) 
        }
    }, []);


  return (
    <div>
        <h6 className="u-padding-y">Please identify the PEP</h6>
        <div className="sub__form">
            <CustomTextField
                placeholder="Name"
                id={`sec-c-pep-name-${investorId}`}
                label="Name"
                asterisk="*"
                name="pep_name"
                value={investorDetails?.personal_detail?.pep_name}
                onChangeFn={onChangeIn}
                formErrors={errors}
            />
            <CustomTextField
                placeholder="Position"
                id={`sec-c-pep-position-${investorId}`}
                label="Description of Position"
                asterisk="*"
                name="pep_position"
                value={investorDetails?.personal_detail?.pep_position}
                onChangeFn={onChangeIn}
                formErrors={errors}
            />
        </div>
    </div>
  )
}

export default IdentifyPEP