import React from 'react';
import { useSelector } from 'react-redux';
import TaxResidentInformation from './TaxResidentInformation';

const TaxInformation = ({ onFormChange }) => {
  const secC = useSelector((store) => store.secC);

  const onChangeTaxInfo = ({name, value, level, investorId}) => {
    onFormChange({name, value:value, level, investor_id:investorId});
}

  const renderTaxResidentInfo = () => {
    return secC?.investors_details?.map((eachItem, index)=>{
        const tempIndex = index+1
        const uniqId = eachItem?.investor_id || tempIndex
        // return <TaxResidentInformation key={`${index+1}`} investorId={index+1} investorDetails={eachItem} onChangeTaxInfo={onChangeTaxInfo}/> 
        if(index < secC?.no_of_investors ){
          return <TaxResidentInformation key={`${uniqId}`} investorId={uniqId} investorDetails={eachItem} onChangeTaxInfo={onChangeTaxInfo} itemIndex={index}/> 
        }
        return null
      })
}
  
  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">C.2 Tax Information</h4>

        <div className="note__box u-margin-bottom-lg">
          <p><span>Note:</span> Tax Residency rules for individuals differ by country and may be based on the amount of time a person spends in a country, the location of the persons residence or place of work. You should seek professional advice if you are unsure of your Tax Residency status.</p>
        </div>
        {renderTaxResidentInfo()}

        {/* {secC.no_of_investors? Array.from({ length: secC.no_of_investors }, (_, index) => <TaxResidentInformation investorId={index} key={index}/>):null} */}
  </div>
  )
}

export default TaxInformation;