import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import Select from 'react-select';
import countryList from 'react-select-country-list';
import CustomTextField from '../../Layout/CustomTextField';
import CustomRadioInput from '../../Layout/CustomRadioInput';
import {IS_PEP_YES_C, IS_PEP_NO_C} from '../../../../constants';
import { renderErrorTxt } from '../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import IdentifyPEP from './IdentifyPEP';
import moment from 'moment/moment';

const Investors = ({ investorId, investorDetails, investorFormId, onChangeInvestor, itemIndex }) => {

    // useEffect(()=>{
    // //this is breaking the component - running the component continuously
    //     onChangeInvestor({name:'investor_form_id', value:investorFormId, level:'investors', investorId})
    // },[investorId, onChangeInvestor, investorFormId])
    const dateBefore18Years = moment().subtract(18, 'years').format();
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const errMsgs = {
        title : invFormStatus.errors?.title? invFormStatus.errors?.title[itemIndex]: [],
        given_name : invFormStatus.errors?.given_name? invFormStatus.errors?.given_name[itemIndex]: [],
        middle_name : invFormStatus.errors?.middle_name? invFormStatus.errors?.middle_name[itemIndex]: [],
        surname : invFormStatus.errors?.surname? invFormStatus.errors?.surname[itemIndex]: [],
        dob : invFormStatus.errors?.dob? invFormStatus.errors?.dob[itemIndex]: [],
        unit_no : invFormStatus.errors?.unit_no? invFormStatus.errors?.unit_no[itemIndex]: [],
        street_no : invFormStatus.errors?.street_no? invFormStatus.errors?.street_no[itemIndex]: [],
        street_name : invFormStatus.errors?.street_name? invFormStatus.errors?.street_name[itemIndex]: [],
        suburb : invFormStatus.errors?.suburb? invFormStatus.errors?.suburb[itemIndex]: [],
        state : invFormStatus.errors?.state? invFormStatus.errors?.state[itemIndex]: [],
        postcode : invFormStatus.errors?.postcode? invFormStatus.errors?.postcode[itemIndex]: [],
        country : invFormStatus.errors?.country? invFormStatus.errors?.country[itemIndex]: [],
        is_pep : invFormStatus.errors?.is_pep? invFormStatus.errors?.is_pep[itemIndex]: [],
        pep_name: invFormStatus.errors?.pep_name? invFormStatus.errors?.pep_name[itemIndex]: [],
        pep_position: invFormStatus.errors?.pep_position? invFormStatus.errors?.pep_position[itemIndex]: []
      }

    
    const options = useMemo(() => countryList().getData(), []);

    const onChangeRadioIn = ({name, value}) => {
        const convrtToNum = parseInt(value);
        onChangeInvestor({name:'is_pep', value:convrtToNum, level:'investors', investorId})
    }

    const onChangeIn = ({name, value}) => {
        onChangeInvestor({name, value, level:'investors', investorId})
    }

    const countryValue = () => {
        const value = options.find((country) => {
        if(investorDetails?.personal_detail?.country){
          return  country.label?.toLowerCase() === investorDetails?.personal_detail?.country?.toLowerCase();
        }
        return false
        });
        return value || null;
    }

    const onChangeSelectHandler = (value) => {
        onChangeIn({ name: 'country', value: value.label })
    }

  return (
    <div className="u-padding-y u-padding-bottom-0">
        <h5 className="u-padding-y">{`Investor ${itemIndex+1}`}</h5>

        <div className="sub__form">
            <CustomTextField 
                placeholder="Mr / Mrs / Dr / Ms etc"
                id={`sec-c-inv${investorId}-title`}
                label="Title"
                asterisk=""
                name="title"
                value={investorDetails?.personal_detail?.title}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            <CustomTextField 
                placeholder="Given Name"
                id={`sec-c-inv${investorId}-given-name`}
                label="Given Name(s)"
                asterisk="*"
                name="given_name"
                value={investorDetails?.personal_detail?.given_name}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            <CustomTextField 
                placeholder="Middle Name"
                id={`sec-c-inv${investorId}-middle-name`}
                label="Middle Name"
                asterisk=""
                name="middle_name"
                value={investorDetails?.personal_detail?.middle_name}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />
            
            <CustomTextField
                placeholder="Surname"
                id={`sec-c-inv${investorId}-surname`}
                label="Surname"
                asterisk="*"
                name="surname"
                value={investorDetails?.personal_detail?.surname}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />
            
            <CustomTextField
                placeholder="Date of Birth"
                id={`sec-c-inv${investorId}-dob`}
                inputType="date"
                label="Date of Birth"
                asterisk="*"
                name="dob"
                value={investorDetails?.personal_detail?.dob}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
                maxDate={dateBefore18Years?.split("T")[0]}
            />
        </div>

        <h6 className="u-padding-10y">Primary Residential Address (legal residence for the purpose of income tax and/or as specified in identification documents)</h6>

        <div className="sub__form u-margin-bottom-lg">
            
            <CustomTextField
                placeholder="Unit No./Level"
                id={`sec-c-inv${investorId}-unit`}
                label="Unit No./Level"
                asterisk=""
                name="unit_no"
                value={investorDetails?.personal_detail?.unit_no}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            <CustomTextField
                placeholder="Street Number"
                id={`sec-c-inv${investorId}-street-num`}
                label="Street Number"
                asterisk=""
                name="street_no"
                value={investorDetails?.personal_detail?.street_no}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            <CustomTextField
                placeholder="Street Name"
                id={`sec-c-inv${investorId}-street-name`}
                label="Street Name"
                asterisk="*"
                name="street_name"
                value={investorDetails?.personal_detail?.street_name}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            
            <CustomTextField
                placeholder="Suburb/Town"
                id={`sec-c-inv${investorId}-suburb`}
                label="Suburb/Town"
                asterisk="*"
                name="suburb"
                value={investorDetails?.personal_detail?.suburb}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            <CustomTextField
                placeholder="State"
                id={`sec-c-inv${investorId}-state`}
                label="State"
                asterisk="*"
                name="state"
                value={investorDetails?.personal_detail?.state}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            <CustomTextField
                placeholder="Postcode"
                id={`sec-c-inv${investorId}-postcode`}
                label="Postcode"
                asterisk="*"
                name="postcode"
                value={investorDetails?.personal_detail?.postcode}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />

            <div className="country__dropdown u-margin-bottom-m">
                <label>Country <span>*</span></label>
                <Select
                    className={errMsgs?.country?.length && 'incorrect'}
                    id="select-dropdown-country"
                    name="country"
                    autoFocus={false}
                    options={options}
                    placeholder="Select a Country"
                    value={countryValue()}
                    onChange={onChangeSelectHandler}
                />

                {errMsgs?.country?.length?
                <div className="input__desc">
                    {renderErrorTxt(errMsgs?.country)}
                </div>: null}
            </div>
        </div>

        <div className="u-padding-10y">
            <h6>Is this investor a Politically Exposed Person (PEP)? <span>*</span></h6>
            <CustomRadioInput
                name={`is_pep${investorId}`}
                id={`inv${investorId}-is-pep-yes`}
                content="Yes"
                val={IS_PEP_YES_C}
                checked={investorDetails?.personal_detail?.is_pep === IS_PEP_YES_C}
                onChangeFn={onChangeRadioIn}
            />
            <CustomRadioInput
                name={`is_pep${investorId}`}
                id={`inv${investorId}-is-pep-no`}
                content="No"
                val={IS_PEP_NO_C}
                checked={investorDetails?.personal_detail?.is_pep === IS_PEP_NO_C}
                onChangeFn={onChangeRadioIn}
            />
            {errMsgs?.is_pep?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs?.is_pep)}
            </div>: null}
        </div>

        {investorDetails?.personal_detail?.is_pep === IS_PEP_YES_C ? 
            <IdentifyPEP errors={errMsgs} onChangeIn={onChangeIn} investorId={investorId} investorDetails={investorDetails} />
            :
            null
        }

        <div className="note__box u-padding-0">
            <p><span>Note:</span> For definitions of Politically Exposed Person, Controlling Person and Beneficial Owner: <a href="https://www.silcgroup.com" target="_blank" rel="noreferrer">https://www.silcgroup.com</a></p>
        </div>
    </div>
  )
}

export default Investors