import React, {useEffect, useMemo} from 'react';
// import { useSelector } from 'react-redux';
import CustomTextField from '../../../Layout/CustomTextField';
import CustomCheckbox from '../../../Layout/CustomCheckbox';
import NoTinReason from './NoTinReason';
import { YES_C, NO_C, TAX_RESIDENCE_CHKBX_OTHER_C } from '../../../../../constants';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import TaxInfoOtherJurisRadioBtns from './TaxInfoOtherJurisRadioBtns';
import Select from 'react-select';
import countryList from 'react-select-country-list';

function TaxResidentNonAus({investorId, investorDetails, onChangeIn, noTinReasonDropDwn, noTinReasonInp, onChangeTaxResidence, cnUnderSchemeRadioIn, othrJurRadioIn, othrJurDaysRadioIn, incmTaxOthrJur, errMsgs}) {
    // const A="Reason A:  Country of residence does not issue TINs to tax residents"
    // const B="Reason B: Individual has not been issued with a TIN"
    // const C="Reason C: Country of residence does not require the TIN to be disclosed"
    const options = useMemo(() => countryList().getData(), []);

    const countryValue = () => {
        const value = options.find((country) => {
        if(investorDetails?.tax_info?.country_of_tax_resident){
          return  country.label?.toLowerCase() === investorDetails?.tax_info?.country_of_tax_resident?.toLowerCase();
        }
        return false
        });
        return value || null;
    }

    const onChangeSelectHandler = (value) => {
        onChangeIn({ name: 'country_of_tax_resident', value: value.label })
    }

    useEffect(()=>{
        return ()=>{
            onChangeIn({name:'country_of_tax_resident', value:''})
            onChangeIn({name:'tin', value:''})
            onChangeIn({name:'tax_residence_countries', value:[]}) // for onChangeTaxResidence
            // cnUnderSchemeRadioIn({value:''})
            // othrJurRadioIn({value:''})
            // othrJurDaysRadioIn({value:''})
            // incmTaxOthrJur({value:''})
        }
    },[])
  
    return (
    <div className="u-padding-y u-padding-bottom-0">
        <h6 className="u-padding-10y">Please provide tax residency information and Tax Identification Number (TIN) or equivalent</h6>
        <div className="sub__form">
            {/* <CustomTextField
                label="Country of Tax Residence"
                placeholder="Country of Tax Residence"
                asterisk=""
                id={`country-tax-resident-${investorId}`}
                name='country_of_tax_resident'
                value={investorDetails?.tax_info?.country_of_tax_resident}
                // value={taxResCountriesVal()}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            /> */}
            <div className="country__dropdown">
                <label>Country of Tax Residence </label>
                <Select
                    className={errMsgs?.country_of_tax_resident?.length && 'incorrect'}
                    id="select-dropdown-country"
                    name="country_of_tax_resident"
                    autoFocus={false}
                    options={options}
                    placeholder="Select a Country"
                    value={countryValue()}
                    onChange={onChangeSelectHandler}
                />

                {errMsgs?.country_of_tax_resident?.length?
                    <div className="input__desc">
                        {renderErrorTxt(errMsgs?.country_of_tax_resident)}
                    </div>: null}
            </div>
            <CustomTextField
                label="Tax Identification Number (TIN)"
                placeholder="Tax Identification Number"
                asterisk=""
                id={`tax-identification-number-${investorId}`}
                name='tin'
                value={investorDetails?.tax_info?.tin}
                onChangeFn={onChangeIn}
                formErrors={errMsgs}
            />
        </div> 

        {investorDetails?.tax_info?.tin? null : <NoTinReason investorId={investorId} noTinReasonDropDwn={noTinReasonDropDwn} investorDetails={investorDetails} errMsgs={errMsgs} noTinReasonInp={noTinReasonInp}/>}

        {/* <div className="u-margin-bottom">
            <label htmlFor="reason1">If no TIN, list reason <span>*</span></label>
            <Form.Control
            className="u-margin-bottom"
            as="select"
            id="reason1"
            name="reason_for_no_tin"
            value={investorDetails?.tax_info?.reason_for_no_tin}
            onChange={noTinReasonDropDwn}>
                <option></option>
                <option value={SEC_C_NO_TIN_REASON_A}>{A}</option>
                <option value={SEC_C_NO_TIN_REASON_B}>{B}</option>
                <option value={SEC_C_NO_TIN_REASON_C}>{C}</option>
            </Form.Control>

            {errMsgs?.reason_for_no_tin? <div className="input__desc">
                {renderErrorTxt(errMsgs?.reason_for_no_tin)}
            </div>: null}

            {investorDetails?.tax_info?.reason_for_no_tin === SEC_C_NO_TIN_REASON_B? 
            <div className="form-input form-input-full-width u-padding-y u-margin-bottom-0">
                <label htmlFor="sec-g-con-1-exp">Please provide explanation</label>
                <input className="form-input-common" type="text" placeholder="Please provide explanation" id="sec-g-con-1-exp" required name='explanation_for_tin_not_issued' value={investorDetails?.tax_info?.explanation_for_tin_not_issued} onChange={noTinReasonInp}/>
            
            {errMsgs?.explanation_for_tin_not_issued? <div className="input__desc">
                {renderErrorTxt(errMsgs?.explanation_for_tin_not_issued)}
            </div>: null}

            </div>: null}
        </div> */}

        <div className="note__box">
            <p className="note__box--p">If you are a tax resident of more countries, please provide details in a separate sheet.</p>
        </div>

        <div className="u-padding-y u-padding-bottom-0">
            <h6>Please indicate if you are a tax residence in one of the following countries: <span>*</span></h6>
            <div className="flex-container u-padding-10y">

                <CustomCheckbox 
                    name="tax_residence_countries"
                    id={`sec-c-res-antigua${investorId}`}
                    styleType="dark"
                    content="Antigua and Barbuda"
                    // checked={investorDetails?.tax_info?.country_of_tax_resident?.includes("Antigua and Barbuda")}
                    val={0}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(0) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-bahamas${investorId}`}
                    styleType="dark"
                    content="Bahamas"
                    val={1}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(1) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-bahrain${investorId}`}
                    styleType="dark"
                    content="Bahrain"
                    val={2}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(2) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-barbados${investorId}`}
                    styleType="dark"
                    content="Barbados"
                    val={3}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(3) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-cyprus${investorId}`}
                    styleType="dark"
                    content="Cyprus"
                    val={4}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(4) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-dominica${investorId}`}
                    styleType="dark"
                    content="Dominica"
                    val={5}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(5) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-malaysia${investorId}`}
                    styleType="dark"
                    content="Malaysia"
                    val={6}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(6) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-grenada${investorId}`}
                    styleType="dark"
                    content="Grenada"
                    val={7}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(7) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-malta${investorId}`}
                    styleType="dark"
                    content="Malta"
                    val={8}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(8) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-panama${investorId}`}
                    styleType="dark"
                    content="Panama"
                    val={9}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(9) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-qatar${investorId}`}
                    styleType="dark"
                    content="Qatar"
                    val={10}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(10) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-saint-kitts${investorId}`}
                    styleType="dark"
                    content="Saint Kitts and Nevis"
                    val={11}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(11) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-saint-lucia${investorId}`}
                    styleType="dark"
                    content="Saint Lucia"
                    val={12}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(12) }
                    onChangeFn={onChangeTaxResidence}
                />
                
                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-seychelles${investorId}`}
                    styleType="dark"
                    content="Seychelles"
                    val={13}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(13) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-turks-caicos${investorId}`}
                    styleType="dark"
                    content="Turks and Caicos Islands"
                    val={14}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(14) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-uae${investorId}`}
                    styleType="dark"
                    content="United Arab Emirates"
                    val={15}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(15) }
                    onChangeFn={onChangeTaxResidence}
                />

                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-vanuatu${investorId}`}
                    styleType="dark"
                    content="Vanuatu" 
                    val={16}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(16) }
                    onChangeFn={onChangeTaxResidence}
                />
                
                <CustomCheckbox
                    name="tax_residence_countries"
                    id={`sec-c-res-none${investorId}`}
                    styleType="dark"
                    content="None of the above"
                    val={TAX_RESIDENCE_CHKBX_OTHER_C}
                    checked={investorDetails?.tax_info?.tax_residence_countries?.includes(TAX_RESIDENCE_CHKBX_OTHER_C) }
                    onChangeFn={onChangeTaxResidence}
                />
            </div>
            {errMsgs?.tax_residence_countries?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs?.tax_residence_countries)}
            </div>: null}

            {/* <div className="u-padding-y">
                <h6>1. Did you obtain residence rights under a Citizenship by Investment (CBI) or Residence by Investment (RBI) scheme?<span>*</span></h6>
                <CustomRadioInput 
                    name={`rights-cbi-rbi${investorId}`}
                    id={`rights-cbi-rbi-yes${investorId}`}
                    content="Yes"
                    val={YES_C}
                    checked={investorDetails?.tax_info?.has_obtained_residence_rights_under_CBI_or_RBI === YES_C}
                    onChangeFn={cnUnderSchemeRadioIn}
                />
                <CustomRadioInput
                    name={`rights-cbi-rbi${investorId}`}
                    id={`rights-cbi-rbi-no${investorId}`}
                    content="No"
                    val={NO_C}
                    checked={investorDetails?.tax_info?.has_obtained_residence_rights_under_CBI_or_RBI === NO_C}
                    onChangeFn={cnUnderSchemeRadioIn}
                />
                {errMsgs?.has_obtained_residence_rights_under_CBI_or_RBI? <div className="input__desc">
                    {renderErrorTxt(errMsgs?.has_obtained_residence_rights_under_CBI_or_RBI)}
                </div>: null}
            </div>

            <div className="u-padding-y">
                <h6>2. Do you hold residence rights in any other jurisdiction(s)?<span>*</span></h6>
                <CustomRadioInput 
                    name={`other-jurisdictions${investorId}`}
                    id={`other-jurisdictions-yes${investorId}`}
                    content="Yes"
                    val={YES_C}
                    checked={investorDetails?.tax_info?.has_hold_residence_rights_in_other_jurisdictions === YES_C}
                    onChangeFn={othrJurRadioIn}
                />
                <CustomRadioInput
                    name={`other-jurisdictions${investorId}`}
                    id={`other-jurisdictions-no${investorId}`}
                    content="No"
                    val={NO_C}
                    checked={investorDetails?.tax_info?.has_hold_residence_rights_in_other_jurisdictions === NO_C}
                    onChangeFn={othrJurRadioIn}
                />
                {errMsgs?.has_hold_residence_rights_in_other_jurisdictions? <div className="input__desc">
                    {renderErrorTxt(errMsgs?.has_hold_residence_rights_in_other_jurisdictions)}
                </div>: null}
            </div>

            <div className="u-padding-y">
                <h6>3. Have you spent more than 90 days in any other jurisdiction(s) during the previous year?<span>*</span></h6>
                <CustomRadioInput 
                    name={`90-days-other-jurisdictions${investorId}`}
                    id={`90-days-other-jurisdictions-yes${investorId}`}
                    content="Yes"
                    val={YES_C}
                    checked={investorDetails?.tax_info?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year === YES_C}
                    onChangeFn={othrJurDaysRadioIn}
                />
                <CustomRadioInput
                    name={`90-days-other-jurisdictions${investorId}`}
                    id={`90-days-other-jurisdictions-no${investorId}`}
                    content="No"
                    val={NO_C}
                    checked={investorDetails?.tax_info?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year === NO_C}
                    onChangeFn={othrJurDaysRadioIn}
                />
                {errMsgs?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year? <div className="input__desc">
                    {renderErrorTxt(errMsgs?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year)}
                </div>: null}
            </div>

            <div className="u-padding-y">
                <h6>4. Have you filed income tax returns in any other jurisdiction(s) during the previous year?<span>*</span></h6>
                <CustomRadioInput 
                    name={`tax-returns-other-jurisdictions${investorId}`}
                    id={`tax-returns-other-jurisdictions-yes${investorId}`}
                    content="Yes"
                    val={YES_C}
                    checked={investorDetails?.tax_info?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year === YES_C}
                    onChangeFn={incmTaxOthrJur}
                />
                <CustomRadioInput
                    name={`tax-returns-other-jurisdictions${investorId}`}
                    id={`tax-returns-other-jurisdictions-no${investorId}`}
                    content="No"
                    val={NO_C}
                    checked={investorDetails?.tax_info?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year === NO_C}
                    onChangeFn={incmTaxOthrJur}
                />
                {errMsgs?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year? <div className="input__desc">
                    {renderErrorTxt(errMsgs?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year)}
                </div>: null}
            </div> */}

            {investorDetails?.tax_info?.tax_residence_countries?.includes(TAX_RESIDENCE_CHKBX_OTHER_C)? null:
                <TaxInfoOtherJurisRadioBtns investorId={investorId} investorDetails={investorDetails} cnUnderSchemeRadioIn={cnUnderSchemeRadioIn} othrJurRadioIn={othrJurRadioIn} othrJurDaysRadioIn={othrJurDaysRadioIn} incmTaxOthrJur={incmTaxOthrJur} errMsgs={errMsgs}/>
            }

            {investorDetails?.tax_info?.is_tax_resident_of_non_aus_country === YES_C || investorDetails?.tax_info?.has_hold_residence_rights_in_other_jurisdictions === YES_C || investorDetails?.tax_info?.has_spent_more_than_90_days_in_other_jurisdiction_in_prev_year === YES_C || investorDetails?.tax_info?.has_filed_tax_returns_in_other_jurisdiction_in_prev_year === YES_C? 
            <div className="note__box u-padding-10y"><p className="note__box--p">Please update Part C.2 - Country of Tax Residence and related Taxpayer Identification Number (TIN) or equivalent reflecting the above resulting jurisdictions or provide a reasonable explanation of why the jurisdiction is not a tax residency.</p></div>:null}
        </div>
    </div>
  )
}

export default TaxResidentNonAus;