import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import DocPlaceholder from '../../../Layout/DocPlaceholder';
import { uploadFiles, deleteFile } from '../../../../../actions/Investor-Application-Form/uploadDocActions';
import { LOADING, SUCCESS, FAILED, IDENTIFICATION_DOCUMENTS_SEC_C, SEC_C_DOC_EXTRA_LABEL_MAIN } from '../../../../../constants';
import CustomDropdown from '../../../Layout/CustomDropdown';
import { addNewDocToArray, removeDocFromArray, updateDocArray } from '../../../../../utils/documentUploads';
import { useToast } from '../../../../CustomToast';

const IdentificationDocs = ({onFormChange, investorFormId}) => { 
    const toast = useToast();
    const secC = useSelector((store) => store.secC);

    const [selectedDocType, setSelectedDocType] = useState({label:"Select type of document", value:""})
    const [selectedInvestorNum, setSelectedInvestorNum] = useState({label:`${SEC_C_DOC_EXTRA_LABEL_MAIN} - 1`, value:1})  //{label:"Select Investor", value:""}
    const [uploadedDocs, setUploadedDocs] = useState([])

    const onUploadFile = async(file, type={label:'', value:''}, extraDetails={label:'', value:''}) =>{ //call api to upload files
        if(file){                                 
            const fileName = file.name                   
            const metadata = JSON.stringify([{"owner":`${SEC_C_DOC_EXTRA_LABEL_MAIN} ${extraDetails.value}`}])
            const formData = new FormData();
            formData.append('file', file);
            formData.append('type', type.value);
            // formData.append('index', extraDetails.value);
            formData.append("metadata", metadata)
            formData.append('section', 'c');
            formData.append('investor_form_id', investorFormId);
            const {success, url, data} = await uploadFiles(formData)
            const extradata = {
                loader: success? SUCCESS : FAILED,
                docUrl: url,
                resData: data
            }
            updateDocArray({setUploadedDocs, fileName, extradata})
        }
    }

    const onAddFiles = (files) => {
        //add selected files(by user) to local state - and call api
        addNewDocToArray({
            files,
            uploadedDocs,
            setUploadedDocs,
            onUploadFile,
            doNotShowExtraLabel: ["dummy"],
            selectedDocType:{...selectedDocType},
            selectedEntityNum:{...selectedInvestorNum} 
        })
    }

    const onClickRemove = (fileName) => { 
        //just remove the file from local state
        removeDocFromArray({fileName, uploadedDocs, setUploadedDocs})
    }

    const updateSelectedOption = (option) => {
        //update selected document type to the state
        setSelectedDocType({...option})
    }

    const updateSelectedInvestorNum = (option) => {
        setSelectedInvestorNum({...option})
    }

    const onClickDel = async({fileName, docData}) => {
        //delete the file from aws & remove the file from local state
        updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: true}})
        const { success } = await deleteFile(docData?.resData);
        success && onClickRemove(fileName);
        success && toast.success("Deleted successfully!");
        success || updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: false}})
        success || toast.error("Please try again!")
    }

    const inestorOptions = () => {
        const tempOptionsArr = [];
        const tempNoOfInvestors = secC?.no_of_investors || 1
        for (let i = 1; i <= tempNoOfInvestors; i++) {
            tempOptionsArr.push([i, `${SEC_C_DOC_EXTRA_LABEL_MAIN} - ${i}`])
        }
        return tempOptionsArr
    }

   return (
    <div className="section__container">
        <h4 className="section__container--subtitle">C.3 Identification Documents</h4>

        <div>
            <h5>1. Provide ONE of the following documents for each individual investor:</h5>
            <ul>
                <li>Australian State / Territory driver's licence containing a photograph of the person;</li>
                <li>Australian passport (a passport that has expired within the preceding 2 years is acceptable); <strong>or</strong></li>
                <li>Card issued under an Australian State or Territory for the purpose of proving a person's age containing a photograph of the person</li>
            </ul>
        </div>
        
        <div className="divider-or u-margin-bottom"></div>
        
        <div>
            <h5>2. Provide TWO of following documents for each individual investor:</h5>
            <ul>
                <li>Foreign passport or similar travel document containing a photograph and the signature of the person*; <strong>and</strong></li>
                <li>Foreign driver's licence that contains a photograph of the person in whose name the document is issued* <strong>or</strong></li>
                <li>National ID card issued by a foreign government containing a photograph and a signature of the person in whose name the card was issued*</li>
            </ul>
        </div>
 
        <div className="note__box">
            <p className="note__box--p">* Documents that are written in a language other than English must be accompanied with an English translation prepared by an Accredited Translator.</p>
            <p className="note__box--p">If you are unable to provide one of the above documents, please contact us at <a href="mailto:investors@silcgroup.com">investors@silcgroup.com</a></p>
        </div>

        <div className="section__container--box">
            <div>
                <h6 className="u-margin-bottom u-padding-y">Please attach documents</h6>
                <div className="sub__form" style={{gap:16}}>
                    <div style={{minWidth: 350}}>
                        <CustomDropdown 
                            dropdownOptions={inestorOptions()}
                            selectedOption={selectedInvestorNum}
                            updateSelectedOption={updateSelectedInvestorNum}
                        />
                    </div>
                    <div style={{minWidth: 375}}>
                        <CustomDropdown
                            addClass='control-dropdown-height'
                            dropdownOptions={IDENTIFICATION_DOCUMENTS_SEC_C}
                            selectedOption={selectedDocType}
                            updateSelectedOption={updateSelectedOption}
                            extraLabel={`(${SEC_C_DOC_EXTRA_LABEL_MAIN} - ${selectedInvestorNum?.value})`}
                        />
                    </div>
                </div>
                {selectedDocType?.value ?
                    <div className='u-padding-y'> 
                        <DocPlaceholder onAddFiles={onAddFiles} uploadedDocs={uploadedDocs} onClickRemove={onClickRemove} onClickDel={onClickDel} uniqId="identificationdocs_c"/>
                    </div>: null
                } 
            </div>

            <div className="note__box u-padding-10y">
                <p className="note__box--p">If you prefer not to upload your identification documents, please mail them to The SILC Group, Level 9, 179 Queen St, Melbourne VIC 3000. Your application to invest will not be processed until all required documentation is provided</p>
            </div>
        </div>

  </div>
  )
}

export default IdentificationDocs;